*,*::before,*::after{
    box-sizing: border-box;
}
body {
    font-family: 'Squada One','Noto Sans TC', sans-serif, cursive, -apple-system, BlinkMacSystemFont, "Segoe UI",
      "Noto Sans TC", Roboto, "Helvetica Neue", Arial, sans-serif;
      background: $light;
      font-weight: bold;
  }
a{
    text-decoration: none;
}
img{
    object-fit: cover;
}
